export interface ColumnTable {
  id: 'level' | 'rowName' | 'salary' | 'equipmentCosts' | 'overheads' | 'estimatedProfit';
  name: string;
  minWidth?: number;
}

export function TableColumns() {
  const columns: ColumnTable[] = [
    { id: 'level', name: '' },
    { id: 'rowName', name: ' ', minWidth: 250 },
    { id: 'salary', name: ' ' },
    { id: 'equipmentCosts', name: '' },
    { id: 'overheads', name: ' ' },
    { id: 'estimatedProfit', name: ' ' },
  ];

  return columns;
}
